.TypeaheadItem:before,
.TypeaheadItem:after {
  content: "";
  display: table; }

.TypeaheadItem:after {
  clear: both; }

.Value {
  float: left;
  font-size: 0.83333rem;
  font-weight: 600; }

.Type {
  float: right;
  font-size: 0.77778rem;
  opacity: 0.8; }



